.form-container {
    height: 100vh;
}

.form-signin {
    width: 330px;
    padding: 15px;
    margin: auto;
}

.form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-top-left-radius: 0;
}

.form-signin input[type="password"] {
    margin-bottom: 10px;
    border-bottom-right-radius: 0;
    border-top-left-radius: 0;
}

.login-links a{
    text-decoration: none;
    font-size: 10pt;
}